// vendor
@import "../../src/scss/vendor/swiper-bundle.min.css";
@import "../../src/scss/vendor/normalize.css";

// vars
@import "./variables";

// layouts
@import "./layouts/header";
@import "./layouts/nav";
@import "./layouts/footer";

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
  overflow: auto;
}

body {
  font-family: "Geologica";
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  color: #585a60;
  background-color: #ffffff;
}

.menu-open {
  position: static;
  pointer-events: fill;
  touch-action: none;
  overflow: hidden;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  overflow-x: clip;
}

.content {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  width: 100%;
  flex: 1;
  padding: calc(var(--header-height) + (var(--header-padding-y) * 2)) 0;
  margin-top: calc(-1 * (var(--header-height) * 2));
}

.section {
  contain: layout;
}

.wow {
  visibility: hidden;
}

#mysprite {
  display: none;
}
