:root {
  --blue: #267cdf;
  --blue-rgb: 38, 124, 223;
  --grey: #e2dede;
  --grey-dark: #7f8186;
  --black: #585a60;

  --container-padding: 10px;
}

// fonts
@font-face {
  font-family: "Geologica";
  src: url("../../assets/fonts/Geologica-Light.woff2") format("woff2");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Geologica";
  src: url("../../assets/fonts/Geologica-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Geologica";
  src: url("../../assets/fonts/Geologica-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Geologica";
  src: url("../../assets/fonts/Geologica-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Geologica";
  src: url("../../assets/fonts/Geologica-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Geologica";
  src: url("../../assets/fonts/Geologica-ExtraBold.woff2") format("woff2");
  font-weight: 800;
  font-display: swap;
  font-style: normal;
}

// icon font
@font-face {
  font-family: "stomatfakel2024";
  src: url("../../assets/fonts/stomatfakel2024.eot");
  src:
    url("../../assets/fonts/stomatfakel2024.eot?#iefix") format("embedded-opentype"),
    url("../../assets/fonts/stomatfakel2024.woff") format("woff"),
    url("../../assets/fonts/stomatfakel2024.ttf") format("truetype"),
    url("../../assets/fonts/stomatfakel2024.svg#stomatfakel2024") format("svg");
  font-weight: normal;
  font-style: normal;
}

// icon font config
[data-icon]:before {
  font-family: "stomatfakel2024" !important;
  content: attr(data-icon);
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "stomatfakel2024" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

// icon font vars
.icon-arrow-down:before {
  content: "\61";
}
.icon-arrow-up:before {
  content: "\62";
}
.icon-burger:before {
  content: "\63";
}
.icon-list:before {
  content: "\64";
}
.icon-location:before {
  content: "\65";
}
.icon-phone:before {
  content: "\66";
}
.icon-success:before {
  content: "\67";
}
.icon-close:before {
  content: "\68";
}

// animation
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.fadeEffect {
  opacity: 0;
  & * {
    opacity: 0;
  }
}

// swiper global styles
.swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  border-radius: 20px;
  transition: 0.4s ease;
}
.swiper-pagination-bullet-active {
  width: 49.2px;
  height: 12px;
  background-color: var(--blue);
}

// stretched-link
.stretched-link {
  &::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
  }
}

// H1 48->30; H2 32-24, H3 24->20; p.big16->15; p14->13; p.small 13->12

h1 {
  font-size: 30px;
  @media (min-width: 1200px) {
    font-size: 48px;
  }
}

h2 {
  font-size: 24px;
  @media (min-width: 1200px) {
    font-size: 32px;
  }
}

h3 {
  font-size: 20px;
  @media (min-width: 1200px) {
    font-size: 24px;
  }
}

p {
  font-size: 15px;
  @media (min-width: 1200px) {
    font-size: 16px;
  }
}

p.small {
  font-size: 13px;
  @media (min-width: 1200px) {
    font-size: 14px;
  }
}

p.smaller {
  font-size: 12px;
  @media (min-width: 1200px) {
    font-size: 13px;
  }
}

.section-title {
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: var(--blue);
  text-align: center;
  @media (min-width: 720px) {
    font-size: 32px;
    line-height: 40px;
  }
  @media (min-width: 1200px) {
    font-size: 42px;
    line-height: 52.5px;
  }
}

// container options
.container {
  max-width: 100%;
  width: 100%;
  padding: 0 12px;
  margin: 0 auto;
  // remove old values for container
  // @media (min-width: 720px) {
  //   max-width: 700px;
  // }
  // @media (min-width: 992px) {
  //   max-width: 968px;
  // }
  @media (min-width: 1200px) {
    max-width: 1176px;
  }
  @media (min-width: 1400px) {
    // max-width: 1280px;
    max-width: 1380px;
  }
  @media (min-width: 1600px) {
    max-width: 1600px;
  }
  // @media (min-width: 1200px) {
  //   max-width: 96%;
  // }
}

// components
.btn {
  display: inline-block;
  padding: 15px 40px;
  font-size: 16px;
  font-weight: normal;
  line-height: 120%;
  color: var(--black);
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  cursor: pointer;
  user-select: none;
  text-transform: none;
  border: 1px solid #ffffff;
  border-radius: 5px;
  background-color: #ffffff;
  transition: 0.4s ease;
  &-white {
    background-color: #ffffff;
    &:hover {
      background-color: var(--blue);
      border-color: var(--blue);
      color: #ffffff;
    }
    &:focus:active {
      background-color: rgba(17, 118, 234, 0.4);
      border-color: rgba(17, 118, 234, 0.4);
      color: rgba(255, 255, 255, 0.85);
    }
  }
  &-blue {
    background-color: var(--blue);
    border-color: var(--blue);
    color: #ffffff;
    &:hover {
      background-color: var(--black);
      border-color: var(--black);
      color: #ffffff;
    }
    &:focus:active {
      background-color: rgba(17, 118, 234, 0.4);
      border-color: rgba(17, 118, 234, 0.4);
      color: rgba(255, 255, 255, 0.85);
    }
  }
  &-grey {
    background-color: #ffffff;
    border-color: #ffffff;
    color: #585a60;
    &:hover {
      background-color: var(--black);
      border-color: var(--black);
      color: #ffffff;
    }
    &:focus:active {
      background-color: rgba(88, 90, 96, 0.4);
      border-color: rgba(88, 90, 96, 0.4);
      color: rgba(255, 255, 255, 0.85);
    }
  }
  &-outline {
    &-primary {
      background-color: transparent;
      color: var(--blue);
      border: 1px solid var(--blue);
      &:hover {
        background-color: var(--blue);
        border-color: var(--blue);
        color: #ffffff;
      }
      &:focus:active {
        background-color: rgba(17, 118, 234, 0.4);
        border-color: rgba(17, 118, 234, 0.4);
        color: rgba(255, 255, 255, 0.85);
      }
    }
  }
  &-consult {
    background-color: #eef5fe;
    border-color: #eef5fe;
    color: #585a60;
    &:hover {
      background-color: #585a60;
      border-color: #585a60;
      color: #ffffff;
    }
    &:focus:active {
      background-color: rgba(88, 90, 96, 0.4);
      border-color: rgba(88, 90, 96, 0.4);
      color: rgba(255, 255, 255, 0.85);
    }
  }
}

.input {
  display: inline;
  padding: 16px 22px;
  border-radius: 7px;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: #585a60;
  border: none;
  background-color: #ffffff;
  outline: none;
  transition: 0.4s ease;
  @media (min-width: 720px) {
    padding: 19px 22px;
    font-size: 18px;
    line-height: 22.5px;
  }
  &::placeholder {
    color: #585a60;
  }
  &:focus {
    box-shadow: 0px 0px 0px 2px #585a60;
  }
  &-date {
    position: relative;
    &[type="date"] {
      &::-webkit-calendar-picker-indicator {
        color: rgba(0, 0, 0, 0);
        opacity: 1;
        display: block;
        background: "url('../assets/images/icons/calendar.svg')";
        width: 24px;
        height: 24px;
        border-width: thin;
      }
    }
  }
  &-checkbox {
    position: absolute;
    opacity: 0;
    z-index: -1;
    width: 0;
    height: 0;
    & + label {
      position: relative;
      padding-left: 30px;
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 15px;
      cursor: pointer;
      @media (min-width: 720px) {
        padding-left: 35px;
        font-size: 18px;
        line-height: 22px;
      }
      &::before {
        content: "";
        position: absolute;
        width: 20px;
        height: 20px;
        left: 0;
        top: -1px;
        border-radius: 4px;
        border: 1px solid var(--blue);
        background: #ffffff;
        @media (min-width: 720px) {
          width: 24px;
          height: 24px;
        }
      }
    }
    &:checked {
      & + label {
        &::before {
          background: url('data:image/svg+xml,<svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M21.3333 0H2.66667C1.2 0 0 1.2 0 2.66667V21.3333C0 22.8 1.2 24 2.66667 24H21.3333C22.8 24 24 22.8 24 21.3333V2.66667C24 1.2 22.8 0 21.3333 0ZM9.33333 18.6667L2.66667 12L4.53333 10.1333L9.33333 14.9333L19.4667 4.8L21.3333 6.66667L9.33333 18.6667Z" fill="white" /></svg>');
          background-color: var(--blue);
          background-size: cover;
          border-radius: 4px;
        }
      }
    }
  }
}
